import { EXAM_QUESTION } from '@/helpers/APIHelper';
import { ProviderContext } from '@/reducer/context';
import { useQuery } from '@apollo/client';
import { Button } from 'antd-mobile';
import React, { useContext, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { ButtonBoxInner, Page, ButtonBox } from '../../styles';
import dateToYear from '../../utils/dateToYear';
import { ExamMainInner, ExamInfoTitle, ExamMain } from '../styles';

const Info: React.FC = () => {
  const history = useHistory();
  const { state, dispatch } = useContext(ProviderContext);
  const { exam: examInfo } = state || {};
  const { exam, examHis } = examInfo || {};
  const { sourceType, subject, fullPoints, date = '', timeLimit } = exam || {};
  const { data: examQuestionData } = useQuery(EXAM_QUESTION, {
    variables: { sourceType, subject, date },
  });
  const { examQuestions } = examQuestionData || {};
  useEffect(() => {
    examQuestions &&
      dispatch?.({ type: 'setExamQuestion', examQuestion: examQuestions });
  }, [dispatch, examQuestions]);
  return (
    <Page>
      <ExamInfoTitle>考试信息</ExamInfoTitle>
      <ExamMain>
        <ExamMainInner>
          种类 ：{sourceType}
          <br />
          科目 ：{subject}
          <br />
          年份 ：{dateToYear(date)}
          <br />
          总分 ：{fullPoints}
          <br />
          时间 ：{timeLimit}分钟
          <br />
          题数 ：{examQuestions?.length || 0}
          <br />
          上次记录 ：{examHis?.previousPoints}
          <br />
        </ExamMainInner>
      </ExamMain>
      <ButtonBox>
        <ButtonBoxInner>
          <Button type="primary" onClick={(): void => history.push('/exam')}>
            开始模拟
          </Button>
        </ButtonBoxInner>
      </ButtonBox>
    </Page>
  );
};

export default Info;
